





































































import { Component, Vue } from 'vue-property-decorator'
import includes from 'lodash/includes'
@Component
export default class PopupFilterTransaction extends Vue {
  filter: Record<string, any> = {
    address: []
  }
  listWallet: Array<Record<string, any>> = [
    {
      id: 5,
      name: 'Lynkey Wallet',
      symbol: 'LYNK',
      icon: 'icon-lin'
    },
    {
      id: 0,
      name: 'Bitcoin Wallet',
      symbol: 'BTC',
      icon: 'icon-btc'
    },
    {
      id: 1,
      name: 'Ethereum Wallet',
      symbol: 'ETH',
      icon: 'icon-eth'
    },
    {
      id: 2,
      name: 'Tether Wallet',
      symbol: 'USDT',
      icon: 'icon-usdt',
      disabled: true
    },
    {
      id: 3,
      name: 'USDC Wallet',
      symbol: 'USDC',
      icon: 'icon-usdc',
      disabled: true
    },
    {
      id: 4,
      name: 'BNB Wallet',
      symbol: 'BNB',
      icon: 'icon-bnb',
      disabled: true
    }
  ]

  listStatus: Array<Record<string, any>> = [
    {
      id: 0,
      label: 'Succsess'
    },
    {
      id: 1,
      label: 'Pending'
    },
    {
      id: 2,
      label: 'Error'
    }
  ]

  onlyNumber(event: KeyboardEvent, type: string): void {
    let keyCode = event.keyCode ? event.keyCode : event.which
    //if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
    // 46 is dot
    if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
      event.preventDefault()
    }
    if (keyCode === 46 && includes(this.filter[type], '.')) {
      event.preventDefault()
    }
  }

  numberFormat(event: FocusEvent): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = fnumber.replaceAll(',', '')
      fnumber = this.$options.filters?.numberWithCommas(fnumber)
      _event.target.value = fnumber
    }
  }
}
